export default class Machine extends Component {
    constructor(props) {
        super(props)

        this.state = {
            ring: "",
            checkIfItIsFirstBtnClick: true,
            isWon: false,
        }
    }

    spin = () => {
        this.setState({
            isWon: false,
        })
        let seed = Math.floor(Math.random() * 32)
        let arrayOfProperties = []
        for (let i = 0; i <= 32; i += 1) {
            arrayOfProperties.push(
                `rotateX(${i === 0 ? "-3465" : `${-3465 + -11.25 * i}`}deg)`
            )
        }

        if (!this.state.checkIfItIsFirstBtnClick) {
            this.ring1.style.transform = "rotateX(11.25deg)"
            this.ring2.style.transform = "rotateX(11.25deg)"
            this.ring3.style.transform = "rotateX(11.25deg)"
        }

        setTimeout(() => {
            this.ring1.style.transform = arrayOfProperties[seed]
            this.ring2.style.transform = arrayOfProperties[seed]
            this.ring3.style.transform = arrayOfProperties[seed]
        }, 1000)
        setTimeout(() => {
            this.setState({
                isWon: true,
            })
        }, 4500)

        this.setState({
            ring: `transform: ${arrayOfProperties[seed]}`,
            checkIfItIsFirstBtnClick: false,
        })
    }

    render() {
        const countSlots = slots.length
        // console.log(Math.round(140 / 2 / Math.tan(Math.PI / 32)))

        const slotsAngle = 360 / countSlots

        const slots1 = slots.map((e, i) => {
            return (
                <div
                    className="slot-border slot-1"
                    style={{
                        transform: `rotateX(${slotsAngle *
                            i}deg) translateZ(711px)`,
                    }}
                >
                    <div className="slot1">
                        <img src={`https://picsum.photos/id/${i}/250`} />
                    </div>
                </div>
            )
        })

        const slots2 = slots.map((e, i) => {
            return (
                <div
                    className="slot-border slot-2"
                    style={{
                        transform: `rotateX(${slotsAngle *
                            i}deg) translateZ(711px)`,
                    }}
                >
                    <div className="slot2">
                        <div className="test" style={{ marginBottom: ".5rem" }}>
                            Dla Dzieci
                        </div>
                        <div className="test">I młodzieży</div>
                    </div>
                </div>
            )
        })

        const slots3 = slots.map((e, i) => {
            return (
                <div
                    className="slot-border slot-3"
                    style={{
                        transform: `rotateX(${slotsAngle *
                            i}deg) translateZ(711px)`,
                    }}
                >
                    <div className="slot3">
                        <div>
                            Stypendia dla dzieci i młodzieży realizującej
                            projekty społeczne.
                        </div>
                    </div>
                </div>
            )
        })

        return (
            <Container className="test">
                <ScrollContainer>
                    Scroll down
                    <ScrollMouseY>
                        <ScrollScroller />
                    </ScrollMouseY>
                </ScrollContainer>

                <MachineContainer>
                    <SpinningHandContainer>
                        <SpinningHandRectangle1 />
                        <SpinningHandRectangle2>
                            <SpinningHandCircle />
                            <SpinningHandUnderCircle />
                            <SpinningHandUnderCircleSilver />
                            <SpinningHandLongRectangle />
                        </SpinningHandRectangle2>
                        <SpinningHandHalfCircle />
                    </SpinningHandContainer>
                    <Ad />
                    <MainContainer>
                        <WinList />
                        <PlayMachineBorder>
                            <PlayMachineContainer>
                                <WonSection isWon={this.state.isWon} />
                                <MachineSection>
                                    <MachineSectionShadow />
                                    <MachineSectionLine />
                                    <MachineRing1
                                        id="ring1"
                                        css={this.state.ring}
                                        ref={node => (this.ring1 = node)}
                                    >
                                        {slots1}
                                    </MachineRing1>
                                    <MachineRing2
                                        id="ring2"
                                        css={this.state.ring}
                                        ref={node => (this.ring2 = node)}
                                    >
                                        {slots2}
                                    </MachineRing2>
                                    <MachineRing3
                                        id="ring3"
                                        css={this.state.ring}
                                        ref={node => (this.ring3 = node)}
                                    >
                                        {slots3}
                                    </MachineRing3>
                                </MachineSection>
                            </PlayMachineContainer>
                        </PlayMachineBorder>
                    </MainContainer>
                    <MachineFooterBorder>
                        <MachineFooterContentBorder>
                            <MachineFooterContainer>
                                <MachineFooterItem>
                                    <MachineFooterItemTitle>
                                        O zrealizowanych projektach możesz
                                        przeczytać na stronie
                                    </MachineFooterItemTitle>
                                    <MachineFooterItemAmount>
                                        <FooterLink href="/">
                                            NeuroN Foundation
                                        </FooterLink>
                                        #WiemCoWspieram
                                    </MachineFooterItemAmount>
                                </MachineFooterItem>
                                {/* <MachineFooterItem>
                                    <MachineFooterItemTitle>
                                        day wins
                                    </MachineFooterItemTitle>
                                    <MachineFooterItemAmount>
                                        12
                                    </MachineFooterItemAmount>
                                </MachineFooterItem>
                                <MachineFooterItem>
                                    <MachineFooterItemTitle>
                                        Last Win
                                    </MachineFooterItemTitle>
                                    <MachineFooterItemAmount>
                                        123
                                    </MachineFooterItemAmount>
                                </MachineFooterItem> */}
                                <MachineFooterItem btn>
                                    <MachineFooterSpinBtn>
                                        <BtnText>Wesprzyj</BtnText>
                                    </MachineFooterSpinBtn>
                                </MachineFooterItem>
                                <MachineFooterItem btn>
                                    <MachineFooterSpinBtn onClick={this.spin}>
                                        <BtnText>Zakręć</BtnText>
                                    </MachineFooterSpinBtn>
                                </MachineFooterItem>
                            </MachineFooterContainer>
                        </MachineFooterContentBorder>
                    </MachineFooterBorder>
                </MachineContainer>
            </Container>
        )
    }
}
